import React, {
  useCallback, useEffect, useImperativeHandle, useMemo, useRef, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, Center, Input, HStack, Button
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import InfiniteScroll from 'react-infinite-scroll-component';
import { useNavigate } from 'react-router-dom';
import Icon from '../../../components/Icon';
import style from './CardList.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { listAllCardByAccount, getRewardsToken } from '../../../store/middlewares/card';
import { apiErrorHandler, preloadImage } from '../../../utils/general';
import Card from '../../../components/Card';
import { updateReplaceCardId, updateSelectedCard } from '../../../store/actionCreators/card';
import { useModuleConfig } from '../../../hooks/module';
import { useDebouncedEffect } from '../../../hooks/app';
import SkeletonLoader from '../../../components/SkeletonLoader';
import Loader from '../../../components/Loader';
import { useDrawer } from '../../../hooks/drawer';
import { eventEmitter, Events } from '../../../utils/events';

const CardType = React.lazy(() => import('../CardCreation/CardType/CardType'));
const CardActivation = React.lazy(() => import('../CardActivation/CardActivation'));
const CardDetails = React.lazy(() => import('../CardDetails/CardDetails'));

type Props = {
  showSearch: boolean,
  showAddCard: boolean,
  cardsPerView: number,
  pagination: boolean,
  showExtendedDetails: boolean,
  showCompactView: boolean,
}

export type CardListRef = {
  cardListRefresh: () => void
}

/**
 * Card list
 * @returns {JSX.Element}
 */
const CardList = React.forwardRef<CardListRef, Props>(({
  showSearch, showAddCard, cardsPerView, pagination, showExtendedDetails, showCompactView
}, ref) => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const page = useRef(1);
  const { open } = useDrawer();
  const navigate = useNavigate();

  const isSeachChanged = useRef(false);
  const debouncedSearchKeyword = useRef('');
  const [searchKeyword, updateSearch] = useState('');
  const mode = useAppSelector((store) => store.core.config.mode);
  const { cards, cardLoading } = useAppSelector((store) => store.card, shallowEqual);
  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const person = useAppSelector((store) => store.user.personDetails, shallowEqual);
  const { isDarkMode } = useAppSelector((store) => store.core.config, shallowEqual);

  const {
    showDOSHRewards
  } = useModuleConfig('cards');

  const programConfig = useAppSelector((store) => store.core.programConfig, shallowEqual);

  const showAddACard = useMemo(() => {
    if (programConfig?.code === '0001') {
      return showAddCard && showCompactView && cards.data.length > 0;
    }
    return showAddCard && showCompactView;
  }, [showAddCard, cards, programConfig, showCompactView]);

  const getCards = useCallback(() => {
    if (account) {
      dispatch(listAllCardByAccount(account.id, cardsPerView, (page.current - 1) * cardsPerView, searchKeyword.length === 0 ? undefined : searchKeyword)).catch(apiErrorHandler);
    }
  }, [account, cardsPerView, page, searchKeyword]);

  const showRewards = useCallback(() => {
    if (person) {
      dispatch(getRewardsToken(person.id))
        .then((data:any) => {
          if (data) {
            const base64encode = btoa;
            const payload:any = {
              jwt: data.accessToken,
            };
            if (mode !== 'live') {
              payload.debugLoggingEnabled = true;
            }
            const payloadString = JSON.stringify(payload);
            const encodedPayload = base64encode(payloadString);
            let url = 'https://poweredby.dosh.com/partners/solidFi/setup/';
            url += encodedPayload;
            window.open(url, '_blank');
          }
        })
        .catch(apiErrorHandler);
    }
  }, [getRewardsToken, person]);

  const addCard = useCallback(() => {
    dispatch(updateReplaceCardId(null));
    open(CardType);
  }, []);

  useEffect(() => {
    // preload art images
    preloadImage(programConfig?.brand.cardArtPhysical);
    preloadImage(programConfig?.brand.cardArtVirtual);

    getCards();

    const unsubscribe = eventEmitter.subscribe(Events.CARD_REFRESH, () => {
      console.log('refresh called');
      page.current = 1;
      getCards();
    });

    return unsubscribe;
  }, [account]);

  useDebouncedEffect(() => {
    if (isSeachChanged.current) {
      page.current = 1;
      debouncedSearchKeyword.current = searchKeyword;
      getCards();
    }
  }, 1000, [searchKeyword, isSeachChanged]);

  useImperativeHandle(ref, () => ({
    cardListRefresh: () => {
      page.current = 1;
      getCards();
    }
  }));

  const addCardDOM = (isHorizontal = false) => (
    <Box
      className={`${style.cardList__add_container} default-bg ${isHorizontal ? style.cardList__add_container_horizontal : ''}`}
      onClick={addCard}
    >
      <Center className={style.cardList__add}>
        <Icon icon="icon-add" className="icon-customizable" size={40} />
        <Text variant="regular" pt="10px">{t('cards_list.addACard')}</Text>
      </Center>
    </Box>
  );

  const addCardRewards = () => (
    <Box
      className={`${isDarkMode ? style.rewards_get_card_dark : ''} ${style.rewards_get_card} ${style.is_large}`}
    >
      <Text className={style.rewards_text}>
        {t('cards_list.earnRewardsBy')}
        <Text px="4px" as="span" color="var(--blue-1)">{t('cards_list.virtualCard')}</Text>
        {t('cards_list.today')}
      </Text>
      <Button
        className={style.rewards_get_card_btn}
        onClick={() => {
          addCard();
        }}
      >
        <Text pr="12px">{t('cards_list.rewardsbtn')}</Text>
        <Icon icon="icon-Arrow-big-right" size={12} className="color-white" />
      </Button>
    </Box>
  );

  const showRewardsCard = () => (
    <Box
      className={`${isDarkMode ? style.see_rewards_dark : ''} ${style.see_rewards}`}
      onClick={() => showRewards()}
    >
      <Text className={style.rewards_text}>
        {t('cards_list.seeRewardsClickTo')}
        <Text px="4px" as="span" color="var(--blue-1)">{t('cards_list.seeRewardsRewards')}</Text>
      </Text>
    </Box>
  );

  const cardList = (isHorizontal = false) => (
    <Box className={`${style.autoCardHeight} ${cards.data.length === 0 ? style.dBlock : ''} `}>
      {cards.data.length === 0 && debouncedSearchKeyword.current.length === 0 && !isSeachChanged.current && showAddCard && programConfig?.code === '0001' && showDOSHRewards
        ? addCardRewards() : (cards.data.length !== 0 && debouncedSearchKeyword.current.length === 0 && programConfig?.code === '0001' && showDOSHRewards ? showRewardsCard() : (cards.data.length === 0 && !showAddACard
          ? (
            <Center py={2}>
              <Text variant="regular" className="text-light" textAlign="center">
                {t('cards_list.noData')}
              </Text>
            </Center>
          ) : null
        )) }
      {cards.data.map((c) => (
        <Box
          key={`card-${c.id}`}
          className={`${style.cardList__card_container} ${showExtendedDetails ? 'has-border' : ''} ${c.cardStatus === 'inactive' ? style['cardList__card_container--freezed'] : ''}`}
          onClick={() => {
            dispatch(updateSelectedCard(c));
            setTimeout(() => {
              open(c.cardStatus !== 'pendingActivation' ? CardDetails : CardActivation, {
                id: c.id
              });
            }, 0);
          }}
        >
          <Card data={c} />
          {showExtendedDetails
            ? (
              <Box pt={3}>
                <HStack className={style.cardList__label_limit}>
                  <Box w="48%">
                    <Text variant="small" textAlign="left">{t('cards_list.label')}</Text>
                    <Text variant="regular" textAlign="left">
                      {c.label}
                    </Text>
                  </Box>
                  <Box w="52%" className={style.cardList__spending_limit}>
                    <Text variant="small" textAlign="left">{t('cards_list.spendingLimit')}</Text>
                    <Text variant="regular" textAlign="left">
                      $
                      {' '}
                      {c.limitAmount}
                    </Text>
                  </Box>
                </HStack>
              </Box>
            ) : null }

        </Box>
      ))}
      { showAddACard
        ? addCardDOM(isHorizontal) : null }
    </Box>
  );

  const viewAll = useCallback(() => {
    navigate('/cards');
  }, []);

  return (
    <Center className="page" height="100%">
      {cardLoading && page.current === 1 && !isSeachChanged.current
        ? (
          <SkeletonLoader name={showCompactView ? 'CardGrid' : 'CardList'} showHeader={!showCompactView} />
        )
        : (
          <Box width="100%" flexDirection="column" height="100%" display="flex">
            <Box p={showCompactView ? '' : '15px'}>
              <HStack width="100%" justifyContent="space-between" mb="24px">
                {/* Heading */}
                <Text variant="large">{t('cards_list.title')}</Text>
                {showCompactView
                  ? (
                    <Text variant="regular" className="text-light" fontSize="16px" cursor="pointer" onClick={viewAll}>
                      {t('cards_list.viewAll')}
                      <Icon style={{ marginLeft: '10px' }} icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                    </Text>
                  ) : null }
              </HStack>
              <HStack mt="16px">
                {
                showSearch && !showCompactView ? (
                /* Input and search */
                  <Box className={style.cardList__search}>
                    <Icon icon="icon-search" className={style.cardList__search__icon} size={18} />
                    <Input
                      placeholder={t('cards_list.searchCards')}
                      variant="primary"
                      className={style.cardList__search__input}
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                        isSeachChanged.current = true;
                        updateSearch(e.target.value);
                      }}
                      value={searchKeyword}
                    />
                  </Box>
                ) : null
                }
                {showAddCard && showSearch && !showCompactView ? (
                  <Center className={`${style.contactAddContainer} button-icon-bg`} onClick={addCard}>
                    <Icon className="dark-color" icon="icon-plus" size={16} />
                    <Text className={`${style.contactAddTxt} dark-color`}>
                      {t('select_contact.add')}
                    </Text>
                  </Center>
                ) : null}
              </HStack>
            </Box>

            {/* Cards */}
            {cardLoading && isSeachChanged.current ? <SkeletonLoader name="CardList" showHeader={false} />
              : !showCompactView
                ? (
                  <div
                    className={style.scrollableDiv}
                    id="scrollableDiv"
                  >
                    <InfiniteScroll
                      dataLength={cards.data.length}
                      next={() => {
                        page.current += 1;
                        getCards();
                      }}
                      hasMore={pagination && cards.data.length < cards.total && cards.total !== 0}
                      loader={<Center my={3}><Loader showModal={false} show /></Center>}
                      endMessage={(
            cardLoading ? null
              : (
                <Center py={2}>
                  <Text variant="regular" className="text-light" textAlign="center">
                    {t(cards.total === 0 ? (showAddCard ? '' : 'cards_list.noData') : 'cards_list.noMoreCards')}
                  </Text>
                </Center>
              )
          )}
                      scrollableTarget="scrollableDiv"
                    >
                      {cardList()}
                    </InfiniteScroll>
                  </div>
                ) : (
                  <div
                    className={style.scrollableDivHorizontal}
                    id="scrollableDiv"
                  >
                    {cardList(true)}
                  </div>
                ) }
          </Box>
        )}

    </Center>
  );
});

export default CardList;
